.App {
  background: #000;
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  overflow: hidden;
  color: white;
}

.battlefield{
  height: 20rem;
  width: 20rem;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
}

.field{
  border: 1px solid white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: sans-serif;
  font-size: 5rem;
}

.field:active{
  background: mistyrose;
}

.alert{
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.8);
  font-family: 'Montserrat', sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 4rem;
}

.alert p{
  margin: 0;
}

.alert button{
  border-radius: 2rem;
  font-size: 1.5rem;
  height: 3rem;
  width: 18rem;
  border: 1px solid grey;
  font-family: 'Montserrat', sans-serif;
  background: white;
    color: #000;
}

.alert button:active{
  padding: 7px;
}

