.home {
    background: #000;
    height: 100vh;
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    overflow: hidden;
    color: white;
    font-family: 'Montserrat', sans-serif;
}

.home form{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    height: 6rem;
    margin-top: 1rem;
}

.home input{
    height: 30px;
    font-size: 20px;
    font-family: 'Montserrat', sans-serif;
    width: 20rem;
    background: white;
    border: none;
    border-radius: 20px;
}

.home button {
    height: 32px;
    font-size: 20px;
    border: 1px solid white;
    width: 6rem;
    background: none;
    color: white;
    font-family: 'Montserrat', sans-serif;
    display: flex;
    align-items: center;
    justify-content: center;
}

.home button:active{
    padding: 3px;
  }